// Initialize app when DOM is loaded
document.addEventListener('DOMContentLoaded', () => {
    initializeApp();
});

async function initializeApp() {
    // Load component library
    await loadComponentLibrary();
    
    // Hide loading screen and show app
    setTimeout(() => {
        const loading = document.getElementById('loading');
        const app = document.getElementById('app');
        
        if (loading) loading.style.display = 'none';
        if (app) app.style.display = 'block';
        
        console.log('‚úÖ App initialized successfully');
    }, 1000);
    
    // Set up event listeners
    setupEventListeners();
    
    // Initialize Hebrew number formatting
    initializeHebrewFormatting();
    
    // Check for saved data
    checkStoredData();
    
    // Initialize UI components
    initializeUIComponents();
    
    // Initialize theme system
    initializeThemeSystem();
}

async function loadComponentLibrary() {
    // Files with ES6 exports that need dynamic import
    const es6ModuleFiles = [
        './src/utils/israeli-salary-calculator.js',
        './src/utils/hebrew-financial-terms.js', 
        './src/utils/israeli-financial-tips.js',
        './src/components/ThemeToggle.js',
        './src/components/TransactionForm.js',
        './src/components/CategoryManager.js',
        './src/components/index.js',
        './src/data/index.js'
    ];

    // Files with conditional exports that can use script tags
    const componentFiles = [
        './src/utils/israeli-formatting.js',
        './src/components/Button.js',
        './src/components/Input.js', 
        './src/components/Navigation.js',
        './src/components/Modal.js',
        './src/components/Toast.js',
        './src/components/Dashboard.js',
        './src/components/TransactionList.js',
        './src/components/Charts.js'
    ];

    try {
        // Load ES6 modules using dynamic imports first
        for (const file of es6ModuleFiles) {
            try {
                const module = await import(/* @vite-ignore */ file);
                console.log(`üì¶ ES6 Module loaded: ${file}`);
                
                // Make key exports available globally
                if (module.DataAPI) {
                    window.DataAPI = module.DataAPI;
                    console.log('üíæ DataAPI available globally');
                }
                if (module.DataManager) {
                    window.DataManager = module.DataManager;
                    console.log('üíæ DataManager available globally');
                }
                if (module.IsraeliSalaryCalculator) {
                    window.IsraeliSalaryCalculator = module.IsraeliSalaryCalculator;
                }
                if (module.HEBREW_FINANCIAL_TERMS) {
                    window.HEBREW_FINANCIAL_TERMS = module.HEBREW_FINANCIAL_TERMS;
                }
                if (module.ISRAELI_FINANCIAL_TIPS) {
                    window.ISRAELI_FINANCIAL_TIPS = module.ISRAELI_FINANCIAL_TIPS;
                }
                if (module.ThemeToggle) {
                    window.ThemeToggle = module.ThemeToggle;
                }
                if (module.TransactionForm) {
                    window.TransactionForm = module.TransactionForm;
                }
                if (module.CategoryManager) {
                    window.CategoryManager = module.CategoryManager;
                }
                if (module.ComponentUtils) {
                    window.ComponentUtils = module.ComponentUtils;
                }
            } catch (moduleError) {
                console.error(`‚ùå Error loading ES6 module ${file}:`, moduleError);
                // Show user-friendly error for critical components
                if (file.includes('TransactionForm') || file.includes('DataAPI')) {
                    console.warn(`‚ö†Ô∏è Critical component ${file} failed to load - some features may be unavailable`);
                }
                // Continue with other modules even if one fails
            }
        }
        
        // Load regular component files using script tags
        for (const file of componentFiles) {
            await loadScript(file);
        }
        
        console.log('üì¶ Hebrew UI Components loaded successfully');
        console.log('üíæ Data Management System loaded successfully');
        
        // Check if critical components are available
        const criticalComponents = ['Button', 'Toast', 'Modal', 'DataAPI'];
        const missingComponents = criticalComponents.filter(comp => !window[comp]);
        
        if (missingComponents.length > 0) {
            console.warn('‚ö†Ô∏è Some critical components are missing:', missingComponents);
            // Still allow the app to continue, but with warnings
        }
        
    } catch (error) {
        console.error('‚ùå Error loading components:', error);
        
        // Try to show error via basic alert if HebrewToasts is not available
        if (typeof HebrewToasts !== 'undefined') {
            HebrewToasts.error('◊©◊í◊ô◊ê◊î ◊ë◊ò◊¢◊ô◊†◊™ ◊®◊õ◊ô◊ë◊ô ◊û◊û◊©◊ß ◊î◊û◊©◊™◊û◊©');
        } else {
            // Fallback to alert if toast system failed to load
            console.error('Toast system not available - using basic alert');
            alert('◊©◊í◊ô◊ê◊î ◊ë◊ò◊¢◊ô◊†◊™ ◊®◊õ◊ô◊ë◊ô ◊û◊û◊©◊ß ◊î◊û◊©◊™◊û◊© - ◊ó◊ú◊ß ◊û◊î◊§◊ï◊†◊ß◊¶◊ô◊ï◊™ ◊¢◊ú◊ï◊ú◊ï◊™ ◊ú◊î◊ô◊ï◊™ ◊ú◊ê ◊ñ◊û◊ô◊†◊ï◊™');
        }
    }
}

function loadScript(src) {
    return new Promise((resolve, reject) => {
        const script = document.createElement('script');
        script.src = src;
        script.onload = () => {
            console.log(`üì¶ Script loaded: ${src}`);
            resolve();
        };
        script.onerror = (error) => {
            console.error(`‚ùå Failed to load script: ${src}`, error);
            
            // Show user-friendly error for critical components
            if (src.includes('Button') || src.includes('Toast') || src.includes('Modal')) {
                console.warn(`‚ö†Ô∏è Critical UI component ${src} failed to load - some UI features may be unavailable`);
            }
            
            // Don't reject to allow other scripts to continue loading
            resolve(); // Resolve instead of reject to prevent stopping the entire loading process
        };
        document.head.appendChild(script);
    });
}

